import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {HotelService} from "../../../../services/hotel-service/hotel.service";
import {Hotel} from "../../../../model/hotel";
import {PlaceService} from "../../../../services/place-service/place.service";
import {Place} from "../../../../model/place";

@Component({
  selector: 'app-hotel-new',
  templateUrl: './hotel-new.component.html',
  styleUrls: ['./hotel-new.component.sass']
})
export class HotelNewComponent implements OnInit {
  hotel: Hotel;
  private hotelDetail: Hotel;
  placeOption: Place[];
  constructor(
    private router: Router,
    private hotelService: HotelService,
    private placeService: PlaceService
  ) { }

  ngOnInit() {
    this.getPlaces();
  }
  navigationToList() {
    this.router.navigate(['/hotel/list']);
  }
  addHotel() {
    console.log('hotel' , this.hotelDetail);
    this.hotelService.addHotel(this.hotelDetail).subscribe(result => {
      console.log(result);
      this.router.navigate(['/hotel/list']);
    });
  }
    getPlaces() {
    this.placeService.readPlaces().subscribe(place => {
      this.placeOption = [];
      this.placeOption = place;
    });
    }
  getFormValue(event) {
    console.log(event);
    if (event.values) {
      this.hotelDetail = event.values;
    }
  }
}



