import {Component, OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {HotelService} from "../../../../services/hotel-service/hotel.service";
import {Hotel} from "../../../../model/hotel";

@Component({
  selector: 'app-hotel-list',
  templateUrl: './hotel-list.component.html',
  styleUrls: ['./hotel-list.component.sass']
})
export class HotelListComponent implements OnInit {
  hotel: Hotel[];
  hotelOption: any[] = [];

  constructor(private router: Router,
              private hotelService: HotelService) {
  }

  ngOnInit() {
    this.initHotel();
  }

  navigateToNewHotel() {
    this.router.navigate(['/hotel/new']);
  }

  initHotel() {
    this.hotelService.readHotels().subscribe(hotel => {
      // this.hotel = [];
      this.hotel = hotel;
      console.log('hotel', this.hotel);
    });
  }
  deleteHotel(hotelId) {
    this.hotelService.deleteHotel(hotelId).subscribe(result => {
      alert('delete Success');
      this.initHotel();
    }, error => {
      alert('delete not success');
    });
  }
}



