import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {HotelService} from "../../../../services/hotel-service/hotel.service";
import {Hotel} from "../../../../model/hotel";

@Component({
  selector: 'app-hotel-detail',
  templateUrl: './hotel-detail.component.html',
  styleUrls: ['./hotel-detail.component.sass']
})
export class HotelDetailComponent implements OnInit {
  private hotelId;
  private hotelUpdate: Hotel;
  private currentHotelInfo: Hotel;
  isCancelHidden = true;
  private isUpdateBtnDisabled = true;
  path: string
  urlPath: string
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private hotelService: HotelService
  ) { }

  ngOnInit() {
    this.hotelId = this.route.snapshot.params['id'];
    this.init();
  }
  init() {
    this.hotelService.readHotelById(this.hotelId).subscribe(hotel => {
      console.log(hotel);
      this.currentHotelInfo = hotel;
      for (let i = 0 ; i < this.currentHotelInfo.photos.length; i++) {
        this.path = this.currentHotelInfo.photos[0].path;
      }
      this.urlPath = 'https://homesty.herokuapp.com/' + this.path;
    });

  }
  getFormValueChanges(event) {
    this.isUpdateBtnDisabled = true;
    if (event.valid && event.dirty) {
      this.isUpdateBtnDisabled = false;
    }
    if (event.values) {
      this.hotelUpdate = event.values;
    }
  }
  onUpdateBtnClick() {
    this.hotelService.updateHotelById(this.hotelId , this.hotelUpdate).subscribe(hotelUpdate => {
      console.log(hotelUpdate);
      this.router.navigate(['/hotel/list']);
    });
  }
  navigationToList() {
    this.router.navigate(['/hotel/list']);
  }

}



