import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Hotel} from "../../../../model/hotel";
import {FormBuilder, FormGroup} from "@angular/forms";
import {Place} from "../../../../model/place";

@Component({
  selector: 'app-hotel-form',
  templateUrl: './hotel-form.component.html',
  styleUrls: ['./hotel-form.component.sass']
})
export class HotelFormComponent implements OnInit {
  hotel: Hotel
  hotelForm: FormGroup;
  placeOption: Place;
  selectedValue: string
  private hotelDetail: Hotel;
  @Input() set placeList(val) {
    if (val) {

      this.placeOption = val;
      console.log('place' , this.placeOption);
    }
  }
  @Input() set hotelInfo(val) {
    if (val) {
      this.hotel = val;
      this.initFormData();
    }
  };
  @Output() onFormValueChanges = new EventEmitter();

  constructor(private _fb: FormBuilder) { }

  ngOnInit() {
    this.initFormData();
  }

  initFormData() {
    this.hotelForm = this._fb.group({
      name: [this.hotel &&  this.hotel.name || null],
      address: [this.hotel &&  this.hotel.address || null],
      star: [this.hotel &&  this.hotel.star || 0],
      place: [this.hotel &&  this.hotel.place || null],
      point: [this.hotel &&  this.hotel.point || 0],
      longitude: [this.hotel &&  this.hotel.longitude || 0],
      latitude: [this.hotel &&  this.hotel.latitude || 0],
      foodCertification: [this.hotel &&  this.hotel.foodCertification || false],
      envCertification: [this.hotel &&  this.hotel.envCertification || false],
      bussinessCertification: [this.hotel &&  this.hotel.bussinessCertification || false],
      starCertification: [this.hotel &&  this.hotel.starCertification || false],
      fireCertification: [this.hotel &&  this.hotel.fireCertification || false]
    });
    this.hotelForm.valueChanges.subscribe(values => {
      console.log(values);
      let formChanges = {
        valid: this.hotelForm.valid,
        dirty: this.hotelForm.dirty,
        values
      };
      if (this.hotelForm.valid && this.hotelForm.dirty) {
        formChanges = {
          ...formChanges,
          values
        };
      }
      this.onFormValueChanges.emit(formChanges);
    });
  }

}



